<template>
  <div>
    <mrc_ess_iconGroup :is_disabled='readonly'
                       :showEdit="hasBtn('edit-staff-movement')"
                       :show_del="hasBtn('delete-staff-movement')"
                       :showReturn='false'
                       @handleEdit='handleEdit'
                       @handleDel='handleDel'></mrc_ess_iconGroup>
    <staffVariation :disabled='readonly'></staffVariation>
  </div>
</template>

<script>
import { deleteStaffVariation } from '@/service/api/staffmsg.js'
import staffVariation from './staffVariation'
export default {
  components: {
    staffVariation
  },
  props: {},
  data () {
    return {
      readonly: false,
      list: {}
    };
  },
  computed: {},
  created () { },
  watch: {},
  mounted () {
    this.readonly = this.$store.state.iconstatus
    this.$route.meta.title = this.$store.getters.subPage_name
  },
  beforeDestroy () {

  },
  methods: {
    handleEdit () {
      this.readonly = !this.readonly
      this.$store.commit('set_iconstatus', this.readonly)
    },
    handleDel () {
      this.$myConfirm({ content: this.$t('message_Confirm_the_deletion') }).then(res => {
        deleteStaffVariation({ uuid: this.$store.state.uuid }).then(res => {
          if (res.code === '200') {
            this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
            this.$router.push({
              name: 'staffVariation'
            })
          }
        })
      })
    },
  },
};
</script>

